import React, { useState, useRef } from "react";

function AudioPlayer() {
  const [currentAudio, setCurrentAudio] = useState(0);
  const sources = [
    // "https://www.learningcontainer.com/wp-content/uploads/2020/02/Kalimba.mp3",
    "https://cdn.pixabay.com/download/audio/2022/07/13/audio_75c818dbe1.mp3?filename=tomorrow-114848.mp3",
    "https://cdn.pixabay.com/download/audio/2022/05/27/audio_f8242dc80f.mp3?filename=edm-deep-house-ish-female-vocal-112184.mp3"
  ];
  const audioRef = useRef(null);

  const handlePause = (event) => {
    console.log("Audio paused!");
  };

  const handleEnd = (event) => {
    // Handle the end event
    console.log("Audio ended!");
    // Play the next audio
    console.log("Playing the next audio...");
    setCurrentAudio(currentAudio + 1);
    document.title = `Audio: ${currentAudio}`;
  };

  return (
    <div>
      <audio
        ref={audioRef}
        autoPlay
        onPause={handlePause}
        onEnded={handleEnd}
        controls
        src={sources[currentAudio]}
      >
        Your browser does not support the audio tag.
      </audio>
    </div>
  );
}

export default AudioPlayer;
